nav ul{
display: table;
margin: 20px auto;
padding: 0 ;
width: 80%;
text-align: center;
}
nav li{
display: table-cell;
min-width: 50px;
list-style: none;
}
nav button{
display: block;
width: 100%;
text-decoration: none;
color: #555;
padding-bottom: 5px;
background: none;
border: none;
}
nav li.current{
border-bottom: 3px solid #92D050;
}
nav li:hover{
color: #E7DA66;
border-bottom: 3px solid #F0E475;
}