.editFieldContainer {
  width: 100%;
  margin: 0 auto;
  padding-top: 10px;
}

.deleteButton {
  border-radius: 10%;
  background: rgb(239,239,239);
  border: 1px solid rgb(133,133,133);
  padding: 1px 8px;
}

.deleteButton:active {
  background: rgb(250,250,250);
}

/* 記事編集部分 */
.articleField {
  width: 95%;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 50px;
}

.articleFormContainer {
  width: 100%;
}

.tagContainer {
  align-items: center;
  width: 100%;
  height: 100%;
  padding-bottom: 10px;
}

.articleBodyContainer {
  width: 100%;
}

.articleBodyContainer textarea {
  width: 100%;
  height: 100px;
}

/* 画像編集部分 */
.image-konva {
  width: 100%;
}

.konva-cover-pc {
  width: 100%;
  margin: 20px auto;
  border: 1px solid #d1d1d1;
  box-shadow: 0 0 15px #d1d1d1;
}

.konva-cover-sp {
  width: 50%;
}

.editFieldFlex {
  display: flex;
}

.numbersCover {
  display: flex;
}

.circleNumber {
  display: flex;
  text-align: center;
  justify-content: center;
  align-content: center;
  margin: 3px;
}

.circleNumber p {
  width: 18px;
  height: 18px;
  border: 2px solid #383e45;
  border-radius: 50%;
  margin: 0;
  padding: 2px;
  color: #383e45;
  font-size: 13px;
  font-weight: bold;
  cursor: grab;
}

.circleNumber p:active {
  cursor: grabbing;
}

.numbersSlider {
  display: flex;
  padding-top: 10px
}

.returnButton {
  border: none;
  background: none;
  padding: 0;
  padding-right: 10px;
  cursor: pointer;
}

.articleImgCover {
  width: 100%;
}

.whiteSpace {
  white-space: pre-wrap;
}